.game {
  background-color: var(--body-color);
  margin-left: 1.5rem;
  margin-right: 1.5rem;
}
.game__user-data {
  width: 100%;
  display: flex;
  justify-content: space-between;
}
.game__badge {
  border-radius: 0.25rem;
  margin-top: 0.5rem;
  text-align: center;
  background: var(--first-color);
  color: white;
  font-size: var(--m-font-size);
  padding: 0.625rem 0.625rem;
}
.game__hp-bar {
  border-radius: 0.25rem;
  margin-top: 0.5rem;
  width: 100%;
  border: 6px solid red;
}
.game__mp-bar {
  border-radius: 0.25rem;
  margin-top: 0.2rem;
  border: 6px solid blue;
}
.game__img-wraper {
  width: 10rem;
  height: 10rem;
  position: relative;
}
.game .nani::after {
  content: "Nani";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.6);
  width: 100%;
  height: 100%;
  color: red;
  font-weight: bold;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1rem;
}
.game .omae:after {
  content: "omae wa mou shindeiru";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.6);
  width: 100%;
  height: 100%;
  color: red;
  font-weight: bold;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1rem;
}
.game__user-img {
  margin-top: 0.5rem;
  border: 1px solid black;
  border-radius: 0.25rem;
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: top;
  display: block;
}
.game__score-board {
  border: 1px solid black;
  max-width: 12.5rem;
  min-width: 30%;
  padding: 1rem;
  margin: 2rem auto;
  font-size: var(--l-font-size);
  border-radius: 0.25rem;
  text-align: center;
  position: absolute;
  top: 5rem;
  left: 50%;
  transform: translate(-50%);
}
.game__result {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  text-align: center;
  font-size: 1.3rem;
  font-weight: var(--font-medium);
}
.game__text {
  width: max-content;
  margin: 0.9375rem;
  padding: 0 0.3125rem;
  float: right;
  background: #ddd;
  border: 1px solid #222;
  box-shadow: 3px 3px 0 #222;
}
.game__text--user {
  transform: rotate(2deg);
  display: none;
}
.game__text--computer {
  transform: rotate(-2deg);
  display: none;
}
.game__choices {
  display: grid;
  justify-content: center;
  grid-template-columns: repeat(3, max-content);
  flex-wrap: wrap;
  gap: 1rem;
  margin: 2.5rem 1rem;
}
.game__choice {
  display: flex;
  justify-content: center;
  align-items: center;
  border: 4px solid black;
  border-radius: 50%;
  overflow: hidden;
  width: 9.375rem;
  height: 9.375rem;
  background-color: #ffea00;
  transition: all 0.3s ease-in-out;
  margin: 1rem;
}
.game__choice:hover {
  cursor: pointer;
  scale: 1.2;
}
.game__img {
  height: 5.625rem;
  height: 5.625rem;
}
.game__action-message {
  text-align: center;
  font-size: var(--l-font-size);
  font-weight: var(--text-color);
  color: black;
}

.green-glow {
  border: 4px solid #4dcc7d;
  box-shadow: 0 0 10px #31b43a;
}

.red-glow {
  border: 4px solid #fc121b;
  box-shadow: 0 0 10px #d01115;
}

.gray-glow {
  border: 4px solid #464647;
  box-shadow: 0 0 10px #25292b;
}

.header {
  background-color: var(--first-color);
}
.header__title {
  text-align: center;
  padding: 1.5rem;
}

@media (max-width: 29.9375em) {
  .game__img-wraper {
    width: 5rem;
    height: 5rem;
  }

  .game__choices {
    grid-template-columns: repeat(1, max-content);
    gap: 0.5rem;
  }

  .game__choice {
    width: 7.5rem;
    height: 7.5rem;
  }

  .game__img {
    height: 5.625rem;
    height: 5.625rem;
  }
}
html {
  font-size: 100%;
  box-sizing: border-box;
  font: inherit;
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

body {
  font-family: var(--body-font);
  font-size: var(--m-font-size);
  background-color: var(--body-color);
  color: var(--text-color);
}

.visually-hidden {
  position: absolute;
  left: -10000px;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
}

:root {
  --hue-color: 215;
  --first-color-palette: hsl(215, 69%, 61%);
  --first-color-second-palette: hsl(215, 69%, 61%);
  --first-color-second-darken-palette: hsl(216, 68%, 24%);
  --first-color-alt-palette: hsl(215, 57%, 53%);
  --first-color-lighter-palette: hsl(215, 92%, 85%);
  --title-color-palette: hsl(215, 8%, 15%);
  --text-color-palette: hsl(215, 8%, 45%);
  --text-color-light-palette: hsl(215, 8%, 65%);
  --input-color-palette: hsl(215, 70%, 96%);
  --input-color-darken-palette: hsl(215, 79%, 85%);
  --scroll-bar-color-pallete: hsl(0, 91%, 23%);
  --scroll-thumb-color-pallete: hsl(0, 22%, 35%);
  --body-color-palette: hsl(215, 60%, 99%);
  --container-color-palette: #fff;
  --first-color: hsl(var(--hue-color), 69%, 61%);
  --first-color-second: hsl(var(--hue-color), 69%, 61%);
  --first-color-second-darken: hsl(var(--hue-color), 68%, 24%);
  --first-color-alt: hsl(var(--hue-color), 57%, 53%);
  --first-color-lighter: hsl(var(--hue-color), 92%, 85%);
  --title-color: hsl(var(--hue-color), 8%, 15%);
  --text-color: hsl(var(--hue-color), 8%, 45%);
  --text-color-light: hsl(var(--hue-color), 8%, 65%);
  --input-color: hsl(var(--hue-color), 70%, 96%);
  --input-color-darken: hsl(var(--hue-color), 70%, 90%);
  --body-color: hsl(var(--hue-color), 10%, 98%);
  --header-color: hsl(var(--hue-color), 67%, 95%);
  --container-color: #fff;
  --scroll-bar-color: hsl(var(--hue-color), 12%, 90%);
  --scroll-thumb-color: hsl(var(--hue-color), 37%, 31%);
}

body.dark-theme {
  --first-color-second: hsl(var(--hue-color), 30%, 8%);
  --title-color: hsl(var(--hue-color), 8%, 95%);
  --text-color: hsl(var(--hue-color), 8%, 75%);
  --input-color: hsl(var(--hue-color), 29%, 16%);
  --body-color: hsl(var(--hue-color), 28%, 12%);
  --header-color: hsl(var(--hue-color), 67%, 7%);
  --container-color: hsl(var(--hue-color), 29%, 16%);
  --scroll-bar-color: hsl(var(--hue-color), 12%, 90%);
  --scroll-thumb-color: hsl(var(--hue-color), 37%, 31%);
}

@font-face {
  font-family: "SequentialistBB";
  src: url("https://s3-us-west-2.amazonaws.com/s.cdpn.io/4273/306FA6_1_0.woff2") format("woff2"), url("https://s3-us-west-2.amazonaws.com/s.cdpn.io/4273/306FA6_0_0.woff") format("woff");
  font-style: normal;
  font-weight: 400;
}
@font-face {
  font-family: "SequentialistBB";
  src: url("https://s3-us-west-2.amazonaws.com/s.cdpn.io/4273/306FA6_0_0.woff2") format("woff2"), url("https://s3-us-west-2.amazonaws.com/s.cdpn.io/4273/306FA6_0_0.woff") format("woff");
  font-style: italic;
  font-weight: 400;
}
:root {
  --body-font: "SequentialistBB", sans-serif;
}

.container {
  padding: 0;
  max-width: 68.75rem;
  margin: 0 auto;
}

.grid {
  display: grid;
  gap: 1.5rem;
}

:root {
  --header-height: 3rem;
  --xs-font-size: 0.75rem;
  --s-font-size: 0.813rem;
  --m-font-size: 0.938rem;
  --l-font-size: 2rem;
  --h1-font-size: 1.5rem;
  --h2-font-size: 1.25rem;
  --h3-font-size: 1.125rem;
  --font-medium: 500;
  --font-semi-bold: 600;
  --mb-0-25: 0.25rem;
  --mb-0-5: 0.5rem;
  --mb-0-75: 0.75rem;
  --mb-1: 1rem;
  --mb-1-5: 1.5rem;
  --mb-2: 2rem;
  --mb-2-5: 2.5rem;
  --mb-3: 3rem;
  --z-tolltip: 10;
  --z-fixed: 100;
  --z-modal: 1000;
}
@media (min-width: 71.875em) {
  :root {
    --xs-font-size: 0.813rem;
    --s-font-size: 0.875rem;
    --m-font-size: 1rem;
    --l-font-size: 3rem;
    --h1-font-size: 2.25rem;
    --h2-font-size: 1.5rem;
    --h3-font-size: 1.25rem;
  }
}

h1,
h2,
h3,
h4,
h5,
h6 {
  color: var(--title-color);
  font-weight: var(--font-semi-bold);
}

a,
a:visited,
a:active {
  text-decoration: none;
  color: inherit;
}

ul,
li {
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
}

button {
  box-shadow: 0;
  border: 0;
  font-family: inherit;
  /* 1 */
  font-size: 100%;
  /* 1 */
  line-height: 1;
  /* 1 */
  margin: 0;
  /* 2 */
}

textarea {
  resize: none;
}